<template>
    <div class="table">
        <div class="container">
            <div class="handle-box">
                <el-form :inline="true" label-width="80px">
                    <el-form-item label="性别">
                        <el-select
                            v-model="parames.gender"
                            placeholder="请选择性别"
                            class="handle-select"
                        >
                            <el-option key="-1" label="全部" value="全部"></el-option>
                            <el-option key="0" label="男" value="男"></el-option>
                            <el-option key="1" label="女" value="女"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="用户昵称">
                        <el-input
                            v-model="parames.nickName"
                            placeholder="请输入用户昵称"
                            class="handle-input"
                        ></el-input>
                    </el-form-item>
                    <el-form-item label="注册时间">
                        <el-date-picker
                            value-format="timestamp"
                            v-model="createDateList"
                            type="daterange"
                            start-placeholder="开始"
                            end-placeholder="结束"
                            @change="selectTime"
                        ></el-date-picker>
                    </el-form-item>
                    <el-button type="primary" @click="search">搜索</el-button>
                </el-form>
            </div>
            <el-table :data="dataList" border class="table" ref="multipleTable">
                <el-table-column label="头像" min-width="100">
                    <template slot-scope="scope">
                        <div class="goods-info">
                            <div class="goods-img-wrap">
                                <img class="goods-img" :src="scope.row.imgUrl" alt>
                            </div>
                        </div>
                    </template>
                </el-table-column>
                <el-table-column label="名称" prop="nickName" min-width="60"></el-table-column>
                <el-table-column label="性别" prop="gender" min-width="60"></el-table-column>
                <el-table-column label="积分" prop="point" min-width="60"></el-table-column>
                <el-table-column label="注册时间" prop="createTime" min-width="80"></el-table-column>
            </el-table>
            <div class="pagination">
                <my-page :pager="pager" @change="changePage"></my-page>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "userList",
    data() {
        return {
            createDateList: [], //创建时间组
            pager: {
                total: 100,
                page: 1,
                rows: 20
            }, //分页参数

            dataList: [], //列表数据
            parames: {
                nickName: "",
                gender:"全部",
                startTime: -1,
                endTime: -1,
                pageNo: 1,
                pageSize: 20
            } //请求参数
        };
    },
    created() {
        this.getDataList();
    },
    methods: {
        async getDataList() {
            const data = await this.$api.UserList(this.parames);

            if (data && data[0] && data[0].pageNum == 1) {
                this.pager.total = data[0].total;
                this.pager.rows = data[0].pageSize;
            } else if (this.parames.pageNo == 1 && data.length == 0) {
                this.pager.total = 0;
            }

            this.dataList = data;
        },
        //创建时间选择
        selectTime() {
            this.parames.startTime = this.createDateList
                ? this.createDateList[0]
                : -1;
            this.parames.endTime = this.createDateList
                ? this.createDateList[1]
                : -1;
        },
        //搜索
        search() {
            this.getDataList();
        },
        goodsAdd() {
            this.$router.push("/goodsAdd");
        },
        //分页事件
        changePage() {
            this.parames.pageNo = this.pager.page == 0 ? 1 : this.pager.page;
            this.parames.pageSize = this.pager.rows;
            this.getDataList();
        },

        //商品处理
        handleGoods(status, row) {
            if (status === "remark") {
                this.$prompt("请输入备注信息", "提示", {
                    confirmButtonText: "确定",
                    cancelButtonText: "取消",
                    inputValue: row.remark
                })
                    .then(({ value }) => {
                        if (value === "") {
                            this.$message({
                                message: "备注不能为空",
                                type: "warning"
                            });
                        } else {
                            this.goodsRemark(row.goodsId, value);
                        }
                    })
                    .catch(() => {
                        this.$message({
                            type: "info",
                            message: "取消输入"
                        });
                    });
            } else if (status === "del") {
                this.$confirm("删除该商品, 是否继续?", "提示", {
                    confirmButtonText: "确定",
                    cancelButtonText: "取消",
                    type: "warning"
                })
                    .then(() => {
                        this.goodsDel(row.goodsId);
                    })
                    .catch(() => {
                        this.$message({
                            type: "info",
                            message: "已取消删除"
                        });
                    });
            }else if (status === "up") {
                this.$confirm("上架该商品, 是否继续?", "提示", {
                    confirmButtonText: "确定",
                    cancelButtonText: "取消",
                    type: "warning"
                })
                    .then(() => {
                        this.goodsUpDown(row.goodsId,1);
                    })
                    .catch(() => {
                        this.$message({
                            type: "info",
                            message: "已取消"
                        });
                    });
            }else if (status === "down") {
                this.$confirm("下架该商品, 是否继续?", "提示", {
                    confirmButtonText: "确定",
                    cancelButtonText: "取消",
                    type: "warning"
                })
                    .then(() => {
                        this.goodsUpDown(row.goodsId,2);
                    })
                    .catch(() => {
                        this.$message({
                            type: "info",
                            message: "已取消删除"
                        });
                    });
            } else if (status === "edit") {
                this.$router.push(`/goodsEdit?goodsId=${row.goodsId}`);
            }
        },
        //商品备注
        async goodsRemark(goodsId, remark) {
            const data = await this.$api.GoodsRemark(
                { goodsId, remark },
                goodsId
            );
            this.$message({
                message: "备注成功",
                type: "success"
            });
            this.getDataList();
        },
        //商品删除
        async goodsDel(goodsId) {
            const data = await this.$api.GoodsDelete({ goodsId });
            this.$message({
                message: "删除成功",
                type: "success"
            });
            this.getDataList();
        },
        //商品上下架
        async goodsUpDown(goodsId,status) {
            const data = await this.$api.GoodsUpDown({ goodsId ,status});
            this.$message({
                message: "操作成功",
                type: "success"
            });
            this.getDataList();
        }
    }
};
</script>

<style scoped>
.handle-box {
    margin-bottom: 20px;
}

.handle-select {
    width: 120px;
}

.handle-input {
    width: 200px;
    display: inline-block;
}
.del-dialog-cnt {
    font-size: 16px;
    text-align: center;
}
.table {
    width: 100%;
    font-size: 14px;
}
.mr10 {
    margin-right: 10px;
}
.red {
    color: #ff0000;
}
</style>
